import "./App.css";
import Layout from "./Components/Layout/Layout";
import Cart from "./Components/Cart/Cart";
import Product from "./Components/Product/Product";
import Brands from "./Components/Brands/Brands";
import Categories from "./Components/Categories/Categories";
import Home from "./Components/Home/Home";
import {
  Navigate,
  RouterProvider,
  createBrowserRouter,
  createHashRouter,
} from "react-router-dom";
import Notfound from "./Components/Notfound/Notfound";
import Register from "./Components/Register/Register";
import Login from "./Components/Login/Login";
import CounterContextProvider from "./Components/CounterContext/CounterContext";
import AuthContextProvider from "./Contexts/AuthContextProvider";
import ProtectedRoute from "./Components/ProtectedRoute/ProtectedRoute";
import AuthProtectedRoute from "./Components/ProtectedRoute/AuthProtectedRoute";
import ProductDetails from "./Components/ProductDetails/ProductDetails";
import { ToastContainer } from "react-toastify";
import CheckoutDetails from "./Components/CheckoutDetails/CheckoutDetails";
import AllOrders from "./Components/AllOrders/AllOrders";

function App() {
  const router = createBrowserRouter([
    {
      path: "",
      element: <Layout />,
      children: [
        { path: "Home", element: <Home /> },
        { path: "", element: <Navigate to="Home" /> },
        {
          path: "Register",
          element: (
            <AuthProtectedRoute>
              <Register />
            </AuthProtectedRoute>
          ),
        },
        {
          path: "/Login",
          element: (
            <AuthProtectedRoute>
              <Login />
            </AuthProtectedRoute>
          ),
        },
        {
          path: "Cart",
          element: (
            <ProtectedRoute>
              <Cart />
            </ProtectedRoute>
          ),
          children: [
            {
              path: "Address",
              element: (
                <ProtectedRoute>
                  <CheckoutDetails />
                </ProtectedRoute>
              ),
            },
          ],
        },
        { path: "Product", element: <Product /> },
        { path: "Categories", element: <Categories /> },
        { path: "Brands", element: <Brands /> },
        {
          path: "ProductDetails/:id",
          element: (
            <ProtectedRoute>
              <ProductDetails />
            </ProtectedRoute>
          ),
        },
        { path: "Address/:id", element: <CheckoutDetails /> },
        {
          path: "/AllOrders",
          element: (
            <ProtectedRoute>
              <AllOrders />
            </ProtectedRoute>
          ),
        },
        { path: "*", element: <Notfound /> },
      ],
    },
  ]);

  return (
    <>
      <AuthContextProvider>
        <CounterContextProvider>
          <RouterProvider router={router}></RouterProvider>
        </CounterContextProvider>
      </AuthContextProvider>
      <ToastContainer theme="colored" autoClose={700} />
    </>
  );
}

export default App;
