import React, { useContext, useEffect, useState } from 'react'
import axios from 'axios'
import SingleProduct from '../SingleProduct/SingleProduct';
import { CounterContext } from '../CounterContext/CounterContext';
import {useQuery} from 'react-query'
import Loading from '../Loading/Loading';
export default function Product() {

  const [products, setProducts] = useState([])
  let {setCounter} = useContext(CounterContext)

    async function getProducts(){
      return await axios.get('https://ecommerce.routemisr.com/api/v1/products');
      
    }

    let {data, isFetching , isError, isLoading, isSuccess} =useQuery('getProducts', getProducts, {enabled:true} )
    // console.log(data)
  return <>
      {isLoading?
      <Loading/>
      :
      <>
        {/* <div className='mt-5 text-center bg-success text-white fs-1 mb-3'>Products</div> */}
        <main className='row g-4 mt-3'>
            {data?.data.data.map((product)=>
                          <SingleProduct key={product.id} changeCounter={setCounter} id={product.id} product={product}/>
                                )}  
        </main>
        <nav aria-label="Page navigation example">
          <ul className="pagination justify-content-center my-5 pt-5">
            <li className="page-item disabled">
              <a className="page-link">Previous</a>
            </li>
            <li className="page-item"><a className="page-link" href="#">1</a></li>
            <li className="page-item"><a className="page-link" href="#">2</a></li>
            <li className="page-item"><a className="page-link" href="#">3</a></li>
            <li className="page-item">
              <a className="page-link" href="#">Next</a>
            </li>
          </ul>
        </nav>
        </>
      }
</>
}
