import React from 'react'
import Slider from "react-slick";
import mainSlidImg1 from '../../Assets/images/slider-image-1.jpeg'
import mainSlidImg2 from '../../Assets/images/slider-image-2.jpeg'
import mainSlidImg3 from '../../Assets/images/slider-image-3.jpeg'
import sideSlidImg1 from '../../Assets/images/grocery-banner-2.jpeg'
import sideSlidImg2 from '../../Assets/images/blog-img-2.jpeg'
import Product from '../Product/Product'
import CategorySlider from '../CategorySlider/CategorySlider';
export default function Home() {

  var settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    speed: 2000,
    autoplaySpeed: 3000,
  };


  return <>
    <main className='banner row g-0  mt-3 border border-5 border-light shadow  rounded d-flex justify-content-center'>
      <div className='col-9 mt-3 bg-light rounded pt-2 p-2    shadow'>
          <Slider {...settings} arrows={false}>
            <div>
            <img style={{ height : '300px' }} className='w-100 rounded'  src={mainSlidImg1} alt="" />
            </div>
            <div>
            <img style={{height:'300px'}}  className='w-100 rounded' src={mainSlidImg2} alt="" />
            </div>
            <div>
            <img style={{height:'300px'}} className='w-100 rounded'  src={mainSlidImg3} alt="" />
            </div>
          </Slider>
      </div>
      <div className='col-2  align-content-stretch ms-1  mt-3 shadow  pt-1 p-1  pb-0 d-none d-md-block' >
            <div>
            <img style={{height:'150px'}} className='w-100 img-fluid p-1 rounded'  src={sideSlidImg1} alt="" />
            </div>
            <div>
            <img style={{height:'150px'}} className='w-100 img-fluid p-1 pb-0 rounded'  src={sideSlidImg2} alt="" />
            </div>
      </div>
      <CategorySlider/>
    </main>
    <Product />
    </>
}



