import React, { useEffect, useState } from 'react'
import './ProductDetails.module.css'
import { useNavigate, useParams } from 'react-router-dom'
import axios from 'axios'
import Slider from "react-slick";

export default function ProductDetails() {

  const {id} = useParams()
  const [Details, setdDetails] = useState([])
  const navigate = useNavigate()
  const [Loading, setLoading] = useState(false)

  var settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

    async function getproductDetails(){
      setLoading(true)
        const {data} = await axios.get(`https://ecommerce.routemisr.com/api/v1/products/${id}`)
        setdDetails(data.data);
        setLoading(false)
      }

      useEffect(()=>{
          getproductDetails()
      }, [])

  return <>
    {Loading?
      <div className='min-vh-100 d-flex align-items-center '>
        <i className='fas fa-spin fa-spinner fa-2xl m-auto d-block '></i>
      </div>
      :
      <>
      <button onClick={()=>navigate('/Product')} className='btn btn-close d-block ms-auto my-5'></button>
        <main className='row align-items-center mb-5'>
              <div className='col-md-4 mb-sm-4 '>
                <Slider {...settings}>
                        {Details.images?.map((img)=>
                            <img className='w-100' src={img} alt="" />
                        )}
                </Slider>
              </div>
              <div className='col-md-8 d-flex align-items-center justify-content-center'>
                <div className='w-100'>
                    <h5 className='mb-3 fw-bold'>{Details.title}</h5>
                    <p className='text-muted px-3 my-3'>{Details.description}</p>
                    <div>
                        <span>{Details.category?.name}</span>
                        <div className='d-flex justify-content-between  mt-2'>
                            <span>{Details.price} EGP</span>
                            <span >{Details.ratingsAverage} <i className='fa-solid fa-star rating-color'></i></span>
                        </div>
                    </div>
                    <button className='btn bg-main w-100 mt-2 text-white'>+ Add To Cart</button>
                </div>
              </div>
        </main>
      </>
    }
  </>
}
