import React, { useContext, useEffect, useState } from 'react'
import './SingleProduct.module.css'
import {useNavigate} from 'react-router-dom'
import { CounterContext } from '../CounterContext/CounterContext'
import axios from 'axios'
import { toast } from 'react-toastify'
export default function SingleProduct({product, changeCounter}) {

  const [addLoading, setaddLoading] = useState(false)
    let {counter , setcounter} = useContext(CounterContext)

  const navigate = useNavigate()

  async function addToCart(productId){
    setaddLoading(true)
    const {data} = await axios.post('https://ecommerce.routemisr.com/api/v1/cart',{productId:`${productId}`},
    {headers: {
          token:`${localStorage.getItem('ecommToken')}`
      }}
    )
    // console.log(data)
    if(data.status === 'success'){
      toast.success('Product added successfully');
      counter === 0? setcounter(1):setcounter(counter+1);
    }
    setaddLoading(false)
  }

  

  return <>
    <div id={product.id} className='col-xl-2 col-lg-2 col-md-4 col-6  product p-2 cursor-pointer overflow-hidden d-flex flex-column justify-content-between rounded border border-5 border-light'>
                        <img  onClick={()=>navigate(`/ProductDetails/${product.id}`)} className='w-100 d-block rounded product-img d-block m-auto' src={product?.imageCover} alt="" />
                        <div  onClick={()=>navigate(`/ProductDetails/${product.id}`)} className='mt-2 text-lg-start text-md-center container h-100  d-flex flex-column justify-content-between bg-main-light p-2'>
                            <h4 className='product-title fw-bold'>{product?.title.split(' ').slice(0, 6).join(' ')}</h4>
                            <div className='d-flex justify-content-between  mt-3'>
                              <span>{product.price} EGP</span>
                              <span >{product.ratingsAverage} <i className='fa-solid fa-star rating-color'></i></span>
                            </div>
                        </div>
                          {localStorage.getItem('ecommToken') &&
                            <button disabled={addLoading} onClick={()=>addToCart(product.id)} className='btn m-auto bg-main w-100 mt-2 text-white'>
                            {addLoading?
                              <div><i className='fas fa-spin fa-spinner fa-1x'></i></div>
                              :
                              'Add To Cart'
                              }
                            </button>
                            }
                        </div>
                      </>
}
